import { Event } from '../../types';

/*
  全局的事件注册管理
 */
export class OB {
    private readonly eventMap: { [key in Event]?: Function[] };
    public constructor() {
        this.eventMap = {};
    }
    public on(event: Event, cb: Function): void {
        this.eventMap[event] = this.eventMap[event] || [];
        this.eventMap[event].push(cb);
    }

    public off(event: Event, cb: Function): void {
        this.eventMap[event] = this.eventMap[event] || [];
        const index = this.eventMap[event].indexOf(cb);
        if (index !== -1) {
            this.eventMap[event].splice(index, index);
        }
    }
    public emit(event: Event, params: any[] = []): void {
        this.eventMap[event] &&
            this.eventMap[event].forEach((cb): void => {
                cb.call(null, ...params);
            });
    }
}
