import { Bounds, SheetElement } from '../../types';
export class Element implements SheetElement {
    public el: HTMLElement;
    public constructor(tag: HTMLElement | string, className: string = '') {
        if (typeof tag === 'string') {
            this.el = document.createElement(tag);
            this.el.className = className;
        } else {
            this.el = tag;
        }
    }

    public on(method: string, handler: (event: Event) => void): Element {
        this.el.addEventListener(method, handler);

        return this;
    }

    public off(method: string, handler: EventListenerOrEventListenerObject): Element {
        this.el.removeEventListener(method, handler);

        return this;
    }

    public children(...children: (HTMLElement | Element)[]): Element {
        children.forEach((child: HTMLElement | Element): void => {
            if (child instanceof Element) {
                this.el.appendChild(child.el);
            } else {
                this.el.appendChild(child);
            }
        });

        return this;
    }

    public unshiftChildren(...children: (HTMLElement | Element)[]) {
        children.forEach((child: HTMLElement | Element): void => {
            if (child instanceof Element) {
                this.el.insertBefore(child.el, this.el.firstChild);
            } else {
                this.el.insertBefore(child, this.el.firstChild);
            }
        });

        return this;
    }

    public css(name: number | string): string;
    public css(name: number | string | { [key: string]: string | number }, value?: number | string): Element;
    public css(name: number | string | { [key: string]: string | number }, value?: number | string): Element | string {
        if (value === undefined && typeof name === 'string') {
            return this.el.style[name];
        }
        if (typeof name === 'number' || typeof name === 'string') {
            this.el.style[name] = value;
        } else {
            Object.keys(name).forEach((k: string): void => {
                this.el.style[k] = name[k];
            });
        }

        return this;
    }

    public offset(): Bounds;
    public offset(value: { [key: string]: string | number }): Element;
    public offset(value?: { [key: string]: string | number }): Bounds | Element {
        if (typeof value === 'undefined') {
            const { offsetTop, offsetLeft, offsetHeight, offsetWidth } = this.el;

            return {
                top: offsetTop,
                left: offsetLeft,
                height: offsetHeight - offsetTop,
                width: offsetWidth - offsetLeft,
            };
        }
        Object.keys(value).forEach((k: string): void => {
            this.el.style[k] = `${value[k]}px`;
        });

        return this;
    }

    public addClass(cls: string): Element {
        const clsReg = new RegExp(cls, 'ig');
        if (!clsReg.test(this.el.className)) {
            this.el.className = `${this.el.className} ${cls}`;
        }

        return this;
    }

    public removeClass(cls: string): Element {
        const clsReg = new RegExp(cls, 'ig');
        if (clsReg.test(this.el.className)) {
            this.el.className = this.el.className.replace(cls, '');
        }

        return this;
    }

    public html(): string;
    public html(text: string): Element;
    public html(html?: string): string | Element {
        if (html !== undefined) {
            this.el.innerHTML = html;

            return this;
        }

        return this.el.innerHTML;
    }

    public text(): string;
    public text(text: string): Element;
    public text(text?: string): string | Element {
        if (text !== undefined) {
            this.el.innerText = text;

            return this;
        }

        return this.el.innerText;
    }

    public attr(qualifiedName: string, value: string): Element {
        this.el.setAttribute(qualifiedName, value);

        return this;
    }

    public contains(child: HTMLElement): boolean {
        return this.el.contains(child);
    }

    public remove(): void {
        this.el.remove();
    }

    public hide(): Element {
        this.css('display', 'none');

        return this;
    }

    public show(): Element {
        this.css('display', 'block');

        return this;
    }

    public focus(): Element {
        this.el.focus();

        return this;
    }

    public disableChild(key: string) {
        for (let index = 0; index < this.el.childNodes.length; index++) {
            const child = this.el.childNodes[index] as HTMLElement;

            /** 避免重复添加disable类 */
            if (child.className.includes(key) && !child.className.includes('disable')) {
                child.className = `${child.className} disable`;
            }
        }
    }

    public childEnable(key: string) {
        for (let index = 0; index < this.el.childNodes.length; index++) {
            const child = this.el.childNodes[index] as HTMLElement;
            if (child.className.includes(key)) {
                child.className = child.className.replace(' disable', '');
            }
        }
    }

    public hasChild(el: HTMLElement | Element) {
        let isMount = false;

        for (let index = 0; index < this.el.childNodes.length; index++) {
            const child = this.el.childNodes[index] as HTMLElement;

            isMount = el instanceof HTMLElement ? child === el : child === el.el;

            if (isMount) break;
        }

        return isMount;
    }

    public scroll({ top, left }: { top: number; left: number }) {
        this.el.scrollLeft = left;
        this.el.scrollTop = top;
    }
}

export const h = (tag: HTMLElement | string, className: string = ''): Element => new Element(tag, className);
