import hotkeys, { KeyHandler } from 'hotkeys-js';
import { HotkeysProps } from '../../types';

export class HotkeysManager {
    public static KEY = {
        COPY: 'ctrl+c,command+c',
    };

    public constructor(props?: HotkeysProps) {
        if (props && props.hotkeys) {
            Object.keys(props.hotkeys).forEach(key => {
                this.bindHotkeys(key, props.hotkeys[key]);
            });
        }
    }

    public bindHotkeys(keys: string, fn: KeyHandler) {
        hotkeys(keys, fn);
    }

    public unbindHotkeys(keys: string, fn: KeyHandler) {
        hotkeys.unbind(keys, fn);
    }
}
