const imageCache = new Map<string, HTMLImageElement>();

const loadingCache = new Map<string, Promise<HTMLImageElement>>();

export function loadImage(imageUrl: string): Promise<HTMLImageElement> {
    const image = imageCache.get(imageUrl);

    if (image) {
        return new Promise(resolve => {
            resolve(image);
        });
    }

    const loader =
        loadingCache.get(imageUrl) ||
        new Promise((resolve, reject): void => {
            const image: HTMLImageElement = new Image();
            image.src = imageUrl;
            image.onload = (): void => {
                imageCache.set(imageUrl, image);
                resolve(image);
            };
            image.onerror = (error): void => {
                reject(error);
            };
        });

    loadingCache.set(imageUrl, loader);

    return loader;
}

export function getImageData(imageUrl: string): HTMLImageElement {
    return imageCache.get(imageUrl);
}
