import { Color } from './base/color';
import { parseBorder } from '../utils';
import { TextShadow, UnderLine, Cell } from '../../types';
import { widget } from './base';
interface Style {
    // box
    top?: number;
    left?: number;
    width?: number;
    height?: number;

    // font
    color?: Color;
    fontStyle?: string;
    fontWeight?: string;
    fontSize?: string;
    fontFamily?: string;
    textAlign?: string;
    verticalAlign?: string;
    textDecoration?: string;
    lineHeight?: string;
    strikeThrough?: string;
    textShadow?: TextShadow;
    underline?: UnderLine;
    subscript?: boolean;
    supscript?: boolean;

    // background
    backgroundColor?: Color;
    backgroundImage?: string;
    backgroundSize?: string;

    // border
    borderTopColor?: Color;
    borderTopStyle?: string;
    borderTopWidth?: number;
    borderBottomColor?: Color;
    borderBottomStyle?: string;
    borderBottomWidth?: number;
    borderLeftColor?: Color;
    borderLeftStyle?: string;
    borderLeftWidth?: number;
    borderRightColor?: Color;
    borderRightStyle?: string;
    borderRightWidth?: number;

    // others
    rotate?: string;
    newLine?: string;
    verticalText?: string;
    verTextDirection?: string;
    cellJust?: 'NONE' | 'HEIGHT' | 'WIDTH';
    autoWrapInfo?: number[];
}

function digestStyle(props): Style {
    const {
        x = 0,
        y = 0,
        top,
        left,
        width,
        height,
        style = {},
        autoWrapInfo,
    } = props;
    const {
        backgroundSize,
        backgroundImage,
        backgroundColor = '',
        color = '',
        textAlign,
        fontWeight = 'normal',
        fontFamily = 'SimSun',
        fontSize = '9pt',
        fontStyle = 'normal',
        verticalAlign,
        border = '',
        borderTop = '',
        borderBottom = '',
        borderLeft = '',
        borderRight = '',
        rotate,
        textShadow,
        underline,
        strikeThrough,
        lineHeight,
        subscript,
        supscript,
        newLine,
        verticalText,
        verTextDirection,
        cellJust,
    } = style;
    const styles: Style = {};
    // box
    styles.top = top || y;
    styles.left = left || x;
    styles.width = width;
    styles.height = height;

    // font
    styles.color = new Color(color);
    styles.textAlign = textAlign;
    styles.verticalAlign = verticalAlign;
    styles.fontWeight = fontWeight;
    styles.fontSize = fontSize;
    styles.fontFamily = fontFamily;
    styles.fontStyle = fontStyle;
    styles.textShadow = textShadow;
    styles.underline = underline;
    styles.strikeThrough = strikeThrough;
    styles.lineHeight = lineHeight;
    styles.subscript = subscript;
    styles.supscript = supscript;

    // background
    styles.backgroundColor = new Color(backgroundColor);
    styles.backgroundImage = backgroundImage;
    styles.backgroundSize = backgroundSize;

    // border
    if (border) {
        const { width, style, color } = parseBorder(border);
        styles.borderLeftWidth = styles.borderTopWidth = styles.borderBottomWidth = styles.borderRightWidth = width;
        styles.borderLeftStyle = styles.borderTopStyle = styles.borderBottomStyle = styles.borderRightStyle = style;
        styles.borderLeftColor = styles.borderTopColor = styles.borderBottomColor = styles.borderRightColor = new Color(
            color,
        );
    }

    if (borderTop) {
        const { width, color, style } = parseBorder(borderTop);
        styles.borderTopWidth = width;
        styles.borderTopStyle = style;
        styles.borderTopColor = new Color(color);
    }

    if (borderBottom) {
        const { width, color, style } = parseBorder(borderBottom);
        styles.borderBottomWidth = width;
        styles.borderBottomStyle = style;
        styles.borderBottomColor = new Color(color);
    }

    if (borderLeft) {
        const { width, color, style } = parseBorder(borderLeft);
        styles.borderLeftWidth = width;
        styles.borderLeftStyle = style;
        styles.borderLeftColor = new Color(color);
    }

    if (borderRight) {
        const { width, color, style } = parseBorder(borderRight);
        styles.borderRightWidth = width;
        styles.borderRightStyle = style;
        styles.borderRightColor = new Color(color);
    }

    // other
    styles.rotate = rotate;
    styles.newLine = newLine;
    styles.verticalText = verticalText;
    styles.verTextDirection = verTextDirection;
    styles.cellJust = cellJust;
    styles.autoWrapInfo = autoWrapInfo && JSON.parse(`${autoWrapInfo}`);

    return styles;
}

@widget('bi.widget')
export class AbstractWidget {
    protected props: any;
    protected top: number;
    protected left: number;
    protected width: number;
    protected height: number;
    protected style: Style;

    public constructor(props: any) {
        this.props = props;
        const { x = 0, y = 0, width, height, top, left } = props;
        this.width = width;
        this.height = height;
        this.top = top || y;
        this.left = left || x;
        this.style = digestStyle(props);
    }

    public getBounds() {
        return {
            left: this.left,
            top: this.top,
            right: this.left + this.width,
            bottom: this.top + this.height,
            width: this.width,
            height: this.height,
        };
    }

    public getChildren() { }

    public getStyle() {
        return this.style;
    }

    public getText() { }

    public mouseDownHandler = (e: Event, x: number, y: number, click: Cell) => {
        if (this.props.listeners) {
            this.props.listeners.forEach(listener => {
                if (listener.eventName === 'CELL_MOUSE_DOWN') {
                    listener.action?.call(this, e, x, y, click);
                }
            });
        }
    };

    public mouseLeaveHandler = (e: Event, x: number, y: number, click: Cell) => {
        if (this.props.listeners) {
            this.props.listeners.forEach(listener => {
                if (listener.eventName === 'CELL_MOUSE_LEAVE') {
                    listener.action?.call(this, e, x, y, click);
                }
            });
        }
    };

    public clickHandler = (e: Event, x: number, y: number, click: Cell) => {
        // 是否跳过选中等事件
        let skip = false;
        if (this.props.listeners) {
            this.props.listeners.forEach(listener => {
                if (listener.eventName === 'CELL_CLICK') {
                    skip = listener.action?.call(this, e, x, y, click);
                }
            });
        }

        return skip;
    };

    public hoverHandler = (e: Event, x: number, y: number, cell: Cell) => {
        if (this.props.listeners) {
            this.props.listeners.forEach(listener => {
                if (listener.eventName === 'CELL_HOVER') {
                    listener.action?.call(this, e, x, y, cell);
                }
            });
        }
    };

    public dbClickHandler = (e: Event, x: number, y: number, cell: Cell) => {
        if (this.props.listeners) {
            this.props.listeners.forEach(listener => {
                if (listener.eventName === 'CELL_DOUBLE_CLICK') {
                    listener.action?.call(this, e, x, y, cell);
                }
            });
        }
    };

    public getRealWidth(): number {
        return this.width;
    }
}
