import '../polyfill/polyfill';
import './style.less';
import { Config, SheetOptions, Bounds, Cell, ColumnRowData, RowsConfig, ColsConfig } from '../../types';
import { Sheet, Element, h } from '../component';
class FineSheet {
    public sheet: Sheet;
    protected parent: Element;
    protected root: Element;

    public constructor(rootEl: HTMLElement, config: Config, events: Events = {}) {
        if (events.beforeInit && !events.beforeInit()) {
            return;
        }
        events.beforeCreate?.();
        this.parent = h(rootEl, 'sheet-root');
        const { width, height } = this.parent.offset() as Bounds;
        const { tableWidth = width, tableHeight = height } = config;
        const sheetOptions = {
            width: tableWidth,
            height: tableHeight,
            ...config.options,
        };

        this.sheet = new Sheet(sheetOptions, events);

        events.beforeMount?.();
        this.parent.children(this.sheet.el);
        events.mounted?.();
    }

    public loadData(data: SheetOptions): FineSheet {
        this.sheet.loadData(data);

        return this;
    }

    public updateData(): FineSheet {
        return this;
    }

    /**
     * 更新单个单元格
     * @param cell 单元格
     * @param data 更新数据
     */
    public updateCell(cell: Cell, data: { [key: string]: any }) {
        this.sheet.updateCell(cell, data);
    }

    /**
     * 根据单元格坐标获取单元格位置
     * @param data
     */
    public getCellPosition(data: { row: number; col: number }) {
        return this.sheet.getCellPosition(data);
    }

    /**
     * resize
     */
    public resize(w?: number, h?: number): void {
        const { width, height } = this.parent.offset() as Bounds;
        w = w || width;
        h = h || height;
        this.sheet.resize(w, h);
    }

    public insertRows(ri: number, rowData: ColumnRowData, config: RowsConfig): void {
        this.sheet.insertRows(ri, rowData, config);
    }

    public insertCols(ri: number, colData: ColumnRowData, config: ColsConfig): void {
        this.sheet.insertCols(ri, colData, config);
    }

    public deleteRows(ri: number, rc: number): void {
        this.sheet.deleteRows(ri, rc);
    }

    public deleteCols(ci: number, cc: number): void {
        this.sheet.deleteCols(ci, cc);
    }

    /**
     * 销毁全局dom元素
     */
    public tear() {
        this.sheet.tear();
    }
}

export default {
    createSheet(rootEl: HTMLElement, config: Config, events?: Events): FineSheet {
        return new FineSheet(rootEl, config, events);
    },
};

export interface Events {
    beforeInit?: () => boolean;
    beforeCreate?: () => void;
    created?: () => void;
    render?: () => void;
    beforeMount?: () => void;
    mounted?: () => void;
}
