import { createWidget, widget } from '../base';
import { Cell } from 'types';
import { Layout } from './layout';
@widget('bi.htape')
export class Htape extends Layout {
    public items: any[];
    public constructor(props) {
        super(props);
        this.items = props.items || [];
        this.children = [];
        this.digest();
    }

    private digest() {
        const bounds = this.getBounds();
        let left = 0;
        const totalWidth = bounds.width;
        const auto =
            totalWidth -
            this.items.reduce((total, item) => total + (parseFloat(item.width) || 0), 0);
        this.children = this.items.map(item => {
            const widget = item.el;
            widget.top = bounds.top;
            widget.left = bounds.left + left;
            if (item.width) {
                widget.width = item.width;
            } else {
                widget.width = auto;
            }
            widget.height = bounds.height;
            left += widget.width;

            return createWidget(widget);
        });
    }

    public clickHandler = (e: Event, left: number, top: number, click: Cell) => {
        for (let i = 0; i < this.children.length; i++) {
            const bounds = this.children[i].getBounds();
            if (bounds.left <= left && bounds.right > left) {
                return this.children[i].clickHandler.call(null, e, left, top, click);
            }
        }

        return true;
    };
}
