import { Element, h } from '../element';
import { unbindClickoutside, bindClickoutside, emptyFn, blockContextMenu } from '../../utils';
import { MenuItem, OptionsType } from '../../../types';

/**
 * 右键单元格的菜单
 */
export class ContextMenu {
    public el: Element;

    private clickHandler: Function;

    private menuItems: MenuItem[];

    public constructor(
        menuItems: MenuItem[] = [
            {
                key: 'copy',
                icon: '',
                label: '复制',
                accel: '',
            },
        ],
    ) {
        this.el = h('div', 'sheet-menu')
            .css({
                userSelect: 'none',
            })
            .hide();
        this.clickHandler = emptyFn;
        this.menuItems = menuItems;
        this.buildItems();
        blockContextMenu(this.el.el);
        document.body.appendChild(this.el.el);
    }

    public show(x: number, y: number, options: OptionsType): void {
        const { width, height } = this.el.show().offset();
        this.configItems(options);
        let top = y,
            left = x;
        if (window.innerHeight - y <= height) {
            top -= height;
        }
        if (window.innerWidth - x <= width) {
            left -= width;
        }
        this.el.offset({ left, top });
        bindClickoutside(this.el);
    }

    public hide(): void {
        this.el.hide();
        unbindClickoutside(this.el);
    }

    public bindHandler(handler: Function): void {
        this.clickHandler = handler;
    }

    public tear() {
        document.body.removeChild(this.el.el);
    }

    private configItems(options: OptionsType) {
        this.menuItems.forEach(item => {
            options[item.key] ? this.el.childEnable(item.key) : this.el.disableChild(item.key);
        });
    }

    private buildItems(): void {
        this.menuItems.forEach(it => {
            if (it.key === 'separator') {
                this.el.children(h('div', 'sheet-menuseparator'));
            }
            this.el.children(
                h('div', `sheet-menuitem ${it.key}`)
                    .on('mousedown', e => {
                        e.stopPropagation();
                    })
                    .on('click', () => {
                        this.clickHandler(it.key);
                        this.hide();
                    })
                    .children(
                        h('div', 'sheet-menuitem-icon'),
                        h('span', 'sheet-menuitem-label').text(it.label || ''),
                        h('span', 'sheet-menuitem-accel').text(it.accel || ''),
                    ),
            );
        });
    }
}
