import { TextAlign } from '../../../types';

export function getRotatePosition({
    rotate,
    x,
    y,
    width,
    height,
    textAlign,
    boundsWidth,
    baseline,
}: {
    rotate: number;
    x: number;
    y: number;
    width: number;
    height: number;
    textAlign: TextAlign;
    boundsWidth: number;
    baseline: number;
}): {
    baseX: number;
    baseY: number;
    textBaseline: CanvasTextBaseline;
    textAlign: CanvasTextAlign;
} {
    const topLine = y + height / 2 - boundsWidth / 2;
    const bottomLine = y + height / 2 + boundsWidth / 2;

    if (Math.abs(rotate) < 80 || textAlign === 'center') {
        return {
            baseX: x + width / 2 - baseline,
            baseY: y + height / 2,
            textBaseline: rotate > 0 ? 'top' : 'bottom',
            textAlign: 'center',
        };
    }

    if (textAlign === 'left') {
        return {
            baseX: x,
            baseY: rotate < 0 ? topLine : bottomLine,
            textBaseline: rotate > 0 ? 'top' : 'bottom',
            textAlign,
        };
    }

    if (textAlign === 'right') {
        return {
            baseX: x + width,
            baseY: rotate < 0 ? bottomLine : topLine,
            textBaseline: rotate > 0 ? 'bottom' : 'top',
            textAlign,
        };
    }

    return {
        baseX: x,
        baseY: y,
        textBaseline: 'bottom',
        textAlign: 'left',
    };
}
