import { widget, createWidget } from './base';
import { AbstractWidget } from './widget';
@widget('bi.icon')
export class Icon extends AbstractWidget {
    private iconStyle: IconStyle;
    private children: any[];

    public constructor(props: any) {
        super(props);

        const { width, height, size = 16, backgroundColor, top, left, ...iconStyles } = (props.icon || {}) as Partial<
            IconStyle
        >;

        this.iconStyle = {
            width: width || size,
            height: height || size,
            size,
            backgroundColor: backgroundColor || '',
            top,
            left,
            ...iconStyles,
        };
    }

    public getChildren() {
        if (this.children) {
            return this.children;
        }

        const {
            width: iconWidth,
            height: iconHeight,
            text,
            size,
            backgroundColor,
            top: additonalTop = 0,
            left: additonalLeft = 0,
            ...styleArgs
        } = this.iconStyle;

        const { left, right, height, top, bottom } = this.getBounds();

        this.children = [
            createWidget({
                type: 'bi.absolute',
                left,
                top: top + (height - iconHeight) / 2,
                bottom: bottom - (height - iconHeight) / 2,
                right,
                style: {
                    backgroundColor,
                },
                width: iconWidth,
                height: iconHeight,
                items: [
                    {
                        el: {
                            type: 'bi.label',
                            style: {
                                ...styleArgs,
                                lineHeight: iconHeight - 1, // 设置折叠树的按钮行高比高度小一点，使其永远保持垂直居中
                                fontSize: `${size}px`,
                                verticalAlign: 'middle',
                            },
                            width: iconWidth,
                            height: iconHeight,
                            text,
                        },
                        left: additonalLeft,
                        top: additonalTop,
                        bottom: -additonalTop,
                        right: -additonalLeft,
                        width: iconWidth,
                        height: iconHeight,
                    },
                ],
            }),
        ];

        return this.children;
    }

    public getRealWidth() {
        return this.getChildren().reduce((sum, child) => child.getRealWidth() + sum, 0);
    }
}

interface IconStyle {
    width: number;
    height: number;
    size: number;
    backgroundColor: string;
    top?: number;
    left?: number;
    [key: string]: any;
}
